import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import firebase from 'firebase';
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import Box from "@mui/material/Box";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import AppleIcon from "@mui/icons-material/Apple";
import logo from "../../../assets/images/Logo/studkz_logo.png";
import Google from "../../../assets/images/icons/google.png";
import { login } from "../../../store/action/auth.action";
import { auth, provider, appleProvider } from "../../../config/firebase";
const theme = createTheme();

export default function Login() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const loader = useSelector((state) => state.LoaderReducer.loader);
  const { loginerror, token } = useSelector((state) => state.auth);
  const [myError, setMyError] = useState(null)
  const [myResult, setResult] = useState(null)

  const signInWithGoogle = async () => {
    const data = await auth.signInWithPopup(provider);
    console.log(data);
    const result = await dispatch(login(data));
    if (result) {
      navigate("/dashboard");
    }
  }

  const signInWithApple = async () => {
    const data = await auth.signInWithPopup(appleProvider)
    .then((res) => {
      setResult(res)
    })
    .catch((error) => {
      console.log(error, 'error')
      setMyError(error)
    })
    console.log(data);
    const result = await dispatch(login(data));
    if (result) {
      navigate("/dashboard");
    }
  }

  const handleForgot =  async () => {
    navigate("/forgot-password");
  }

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",

          }}
        >
          <div>
            <span style={{ display: "flex", alignItems: "center" }}>
              <img src={logo} alt="studkz_logo" width={"120"} />
              <p
                style={{
                  fontSize: "30px",
                  color: "olivedrab",
                  marginTop: "30px",
                }}
              >
                Stud.kz
              </p>
            </span>
          </div>

          <Box
            component="form"

            noValidate
            sx={{ mt: 15 }}
          >
            <Button
              type="button"
              fullWidth
              variant="outlined"
              onClick={() => signInWithApple()}
              sx={{ mt: 3, mb: 2 }}
              startIcon={<AppleIcon />}
              style={{
                borderRadius: 10,
                backgroundColor: "#000000",
                color: "white",
                padding: "14px 30px",
                textTransform: "none",
              }}
            >
              Sign in with Apple
            </Button>

            <Button
              type="button"
              fullWidth
              variant="outlined"
              sx={{ mt: 3, mb: 2 }}
              style={{
                borderRadius: 10,
                backgroundColor: "white",
                color: "#000000",
                padding: "14px 30px",
                textTransform: "none",


              }}
              onClick={() => signInWithGoogle()}
            >
              <img src={Google} alt="studkz_logo" width={"30"} />

              Sign in with Google
            </Button>
          </Box>
          <div style={{ color: 'blue', cursor: 'pointer', textAlign: 'end'}} onClick={() => {handleForgot()}}>Forgot Password ?</div>
        </Box>
      </Container>
    </ThemeProvider>
  );
}
