import { SET_TOKEN, SET_LOGIN_ERROR } from "../constant/index";
import axiosinstance from "../../config/axios"
import { toast } from "react-toastify";
let FormData = require('form-data');

export const login = (User) => {
    return async (dispatch) => {
        try {
            let token = new FormData();
            dispatch({ type: "SET_LOADER", payload: true });
            token.append('fireToken', User.user.Aa)
            const { data } = await axiosinstance.post('loginFirebase', token);
            localStorage.setItem('token', data.token);
            localStorage.setItem('refreshToken', data.refreshToken);
            localStorage.setItem('user', JSON.stringify(User.additionalUserInfo.profile));
            dispatch({ type: SET_TOKEN, payload: User.credential.accessToken });
            dispatch({ type: "SET_LOADER", payload: true });

            return true;
        } catch (error) {
            dispatch({ type: "SET_LOADER", payload: false });
            toast.error((error.response?.data && error.response?.data?.error && error.response?.data?.error?.message) || "Something went wrong");
            return false;
        }

    }
}
